.header{min-width:100%;display:flex;justify-content:space-between;align-items:center;gap:4px;position:relative;z-index:10}.header__mobile{display:flex;gap:4px}.header__logo-wrapper{display:flex;gap:12px}.header__logo-wrapper a{display:flex;justify-content:center;align-items:center;gap:6px}.header__logo-wrapper a img{width:24px;height:24px}@media(min-width: 601px){.header__logo-wrapper a img{width:32px;height:32px}}.header__logo-wrapper svg{width:24px;height:24px}.header__text{color:#fff;font-family:"Pokemon-Solid",sans-serif;font-size:24px;cursor:pointer}.header__search{background:#fff;display:flex;align-items:center;justify-content:center;gap:8px;border:1px solid #dedede;border-radius:6px;padding:4px 12px}@media(min-width: 601px){.header__search{padding:4px 12px}}.header__search svg{width:12px;height:12px;cursor:pointer;color:#cbcbcb}.header__search input{border:none;outline:none;flex:1;font-size:1rem;color:#e54222;font-family:"ClashDisplay-Regular","sans-serif";opacity:.4}.header__search input::placeholder{color:#cbcbcb}.header__search-icon{transition:color 1s ease-in-out,transform 1s ease-in-out}.header__search-icon:hover{color:#e54222;transition:color 1.1s ease-in-out,transform .7s ease-in-out;transform:scale(1.1)}